import React, { useState } from 'react'
import DocumentTitle from 'react-document-title'
import ReactGA from 'react-ga'
import { CSSTransition } from 'react-transition-group'

import { URL, EMAIL } from '../constants'
import Skills from './skills'
import Education from './education'
import TextDivider from './textDivider'
import Interests from './interests'
import SplatZone from './splatZone'
import Coop from './coop'
import Experience from './experience'

const Resume = () => {
  const [showCoop, setShowCoop] = useState(false)

  const toggleCoop = () => {
    ReactGA.event({
      category: 'Resume',
      action: 'Toggle Coop Section',
    })

    setShowCoop(!showCoop)
  }

  return (
    <div className="bg">
      <div className="paper container">
        <DocumentTitle title="Terry Bourgeois' Resume" />
        <section id="info" style={{ display: 'flex', flexDirection: 'row' }}>
          <div style={{ flex: '1 1 auto' }}>
            <h1>Terry Bourgeois</h1>
            <div className="title_location">
              <div>Software Developer</div>
              <div className="divider"> - </div>
              <div>Edmonton, Alberta</div>
            </div>
            <div className="contact">
              <div>
                <a href={'mailto:' + EMAIL}>
                  <i className="fas fa-envelope" />
                  <div className="email">{EMAIL}</div>
                </a>
              </div>
              <TextDivider />
              <div id="website">
                <div href={'#'}>
                  <i className="fas fa-globe" />
                  <div className="url">{URL}</div>
                </div>
              </div>
              <div id="website_clickable">
                <a href={URL} target="_blank" rel="noopener noreferrer">
                  <i className="fas fa-globe" />
                  <div className="url">{URL}</div>
                </a>
              </div>
            </div>
            <br />
          </div>

          <SplatZone />
        </section>

        <section id="summary">
          <hr />
          <div className="row">
            <div className="offset-md-2 col-md-8 offset-lg-3 col-lg-6 text-center">
              Software Developer with 10 years of professional experience,
              primarily interested in front-end development for the web and
              mobile apps.
            </div>
          </div>
          <hr style={{ marginBottom: '0px' }} />
        </section>

        <div className="row">
          <div className="col-md-4 col-md-pull-8 sidebar">
            <Skills />
            <Education />
            <Interests />
          </div>

          <div className="col-md-8  col-md-push-4">
            <Experience />

            <div id="coop-summary" onClick={toggleCoop}>
              <hr />
              <div
                style={{
                  display: 'flex',
                  flexDirection: 'row',
                  alignItems: 'center',
                }}
              >
                <div style={{ marginRight: '10px' }}>
                  <i className="fas fa-plus" />
                </div>
                <div>
                  More than 2 years of Co-op work experience as a Software
                  Developer, including a year-long work term in Hamamatsu,
                  Shizuoka, Japan.
                </div>
              </div>
              <hr />
            </div>
          </div>
        </div>
      </div>

      <CSSTransition
        in={showCoop}
        timeout={2000}
        className="paper container page2"
        classNames="page2"
      >
        <div>
          <div className="row">
            <div className="col-md-8 offset-md-4 col-xl-12 offset-xl-0">
              <Coop show={showCoop} />
            </div>
          </div>
        </div>
      </CSSTransition>
    </div>
  )
}

export default Resume
