import React, { Component } from 'react'
import { Transition } from 'react-transition-group'

import { ReactComponent as Blue } from '../img/splat/blue.svg'
import { ReactComponent as Green } from '../img/splat/green.svg'
import { ReactComponent as Orange } from '../img/splat/orange.svg'
import { ReactComponent as Pink } from '../img/splat/pink.svg'
import { ReactComponent as Purple } from '../img/splat/purple.svg'
import { ReactComponent as Teal } from '../img/splat/teal.svg'

const duration = 90

const defaultStyle = {
  transformOrigin: 'center',
  transition: `transform ${duration}ms ease-out`,
  transform: 'scale(0)',
}

const transitionStyles = {
  entering: { transform: 'scale(0)' },
  entered: { transform: 'scale(1)' },
}

const Fade = props => {
  return (
    <Transition in={props.in} timeout={duration}>
      {state => (
        <div
          style={{
            ...defaultStyle,
            ...transitionStyles[state],
          }}
        >
          {props.children}
        </div>
      )}
    </Transition>
  )
}

export default class Splat extends Component {
  static COLOR = {
    BLUE: 1,
    GREEN: 2,
    ORANGE: 3,
    PINK: 4,
    PURPLE: 5,
    TEAL: 6,
  }

  render = () => {
    var styles = {
      height: this.props.styles.position.height,
      top: this.props.styles.position.top,
      right: this.props.styles.position.right,
      position: 'absolute',
      zIndex: this.props.styles.position.z,
    }

    let splatStyles = {
      height: this.props.styles.position.height,
      width: 'auto',
    }

    let splat = <div />
    switch (this.props.color) {
      case Splat.COLOR.BLUE:
        splat = <Blue style={splatStyles} />
        break
      case Splat.COLOR.GREEN:
        splat = <Green style={splatStyles} />
        break
      case Splat.COLOR.ORANGE:
        splat = <Orange style={splatStyles} />
        break
      case Splat.COLOR.PINK:
        splat = <Pink style={splatStyles} />
        break
      case Splat.COLOR.PURPLE:
        splat = <Purple style={splatStyles} />
        break
      case Splat.COLOR.TEAL:
        splat = <Teal style={splatStyles} />
        break
      default:
        splat = <div />
    }

    return (
      <div style={styles}>
        <Fade in={this.props.styles.show}>{splat}</Fade>
      </div>
    )
  }
}
