import React, { Component } from 'react'
import seedrandom from 'seedrandom'
import ReactGA from 'react-ga'

import Splat from './splat'

import './splatZone.scss'

export default class SplatZone extends Component {
  constructor(props) {
    super(props)

    let initialState = { updating: false }

    for (var key in Splat.COLOR) {
      let color = Splat.COLOR[key]
      initialState[color] = this.init(color)
    }

    this.state = initialState
  }
  componentDidMount = () => {
    setTimeout(this.updateSplats, 1000)
    setInterval(this.updateSplats, 7500)
  }
  init = color => {
    return {
      show: false,
      position: this.resetSplatPosition(color, 1),
    }
  }
  hide = color => {
    return {
      show: false,
      position: this.state[color].position,
    }
  }
  showAtNewPosition = (color, zIndex) => {
    let newState = {}
    newState[color] = {
      show: true,
      position: this.resetSplatPosition(color, zIndex),
    }
    this.setState(newState, () => {
      if (this.isUpdateComplete()) {
        this.setState({
          updating: false,
        })
      }
    })
  }
  resetSplatPosition = (color, zIndex) => {
    let rng = seedrandom(new Date().getMilliseconds().toString() + color)

    let maxWidth = 530
    let maxHeight = 150

    // min half of total height, max total height
    let heightRatio = rng()
    if (heightRatio < 0.5) {
      heightRatio += 0.5
    }
    let height = heightRatio * maxHeight

    // given the height, determine lowest postion, or maxTop
    let maxTop = maxHeight - height
    let top = maxTop * rng()

    // use the rightmost quarter
    let right = maxWidth * 0.35 * rng()

    return {
      z: Math.round(zIndex),
      height: height,
      right: right,
      top: top,
    }
  }
  /**
   * Determine if all splats have been shown, and allow click events if so.
   */
  isUpdateComplete = () => {
    let complete = true
    for (var key in Splat.COLOR) {
      let color = Splat.COLOR[key]
      complete = complete && this.state[color].show
    }
    return complete
  }
  /**
   * Randomly assign provided splat a value which serves as
   * both the timeout value before they appear, and their z-index.
   *
   * Using the same value for both timeout and z-index ensures that
   * splats that appear last go on top.
   */
  getSplatOrder = color => {
    let rng = seedrandom(new Date().getMilliseconds().toString() + color)

    // between 100ms and 300ms
    let ratio = rng()
    if (ratio < 0.1) {
      ratio += 0.1
    }

    return ratio * 400
  }
  updateSplats = () => {
    if (this.state.updating) return

    ReactGA.event({
      category: 'Resume',
      action: 'Splat!',
    })

    let newState = { updating: true }

    for (var key in Splat.COLOR) {
      let color = Splat.COLOR[key]
      newState[color] = this.hide(color)
    }

    this.setState(newState, () => {
      for (var key in Splat.COLOR) {
        let color = Splat.COLOR[key]
        let timeout = this.getSplatOrder(color)
        setTimeout(() => this.showAtNewPosition(color, timeout), timeout)
      }
    })
  }
  render() {
    let splats = []
    for (var key in Splat.COLOR) {
      let color = Splat.COLOR[key]
      splats.push(
        <Splat key={color} color={color} styles={this.state[color]} />
      )
    }

    return (
      <div className="splatZone" onClick={this.updateSplats}>
        {splats}
      </div>
    )
  }
}
